//-------------------Color--------------------
$primary-color-main: #121212;
$primary-color-light: #171717;
$primary-color-lighter: #333333;
$secondary-color-main: #636363;
$secondary-color-light: #a3a3a3;

$support-color-main: #2979ff;
$error-color-main: #dc3545;
$success-color-main: #d4edda;

$primary-color-font: #fefefe;
$secondary-color-font: #000000;

//-----------------Dimension--------------

$navbar-height: 64px;

h1 {
  font-size: 2.25em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.125em;
}

h4 {
  font-size: 0.88em;
}

p {
  font-size: 0.88em;
}

a,
button {
  font-size: 0.88em;
}

@media only screen and (max-width: 900px) {
  h1 {
    font-size: 2em;
  }
  h2 {
    font-size: 1.25em;
  }
  h3 {
    font-size: 1em;
  }
}
//------------------Mixin------------------
@mixin FlexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin FlexAround {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@mixin FlexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin FlexStretch {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

@mixin FullDimension {
  width: 100%;
  height: 100%;
}

@mixin ImageBox {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@mixin NoBorderOutline {
  outline: none;
  border: none;
}

@mixin BorderRadius {
  border-radius: 2px;
}

//---------------animation----------------
@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes sliceInFromRight {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes sliceOutFromRight {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
  }
}
